.app__hero {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin-top: 4rem;

    padding: 0 4rem;
}

.app__hero-right{
    display: flex;
    flex-direction: column;
}

.app__hero-text1 {
    font-family: var(--font-base);
    font-weight: 700;
    font-size: 28px;
    line-height: 34px;
    color: black;
    margin-bottom: 10px;
}

.app__hero-text2 {
    font-family: var(--font-base);
    font-size: 44px;
    line-height: 53.64px;
    color: var(--color-voilete);
    font-weight: 700;
    margin-bottom: 10px;
}

.app__hero-text3 {
    font-size: 1rem;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -2%;
    font-family: var(--font-alt);
    margin-bottom: 20px;
}

.app__hero-button {
    display: flex;
    background: var(--color-voilete);
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 30px;
    padding: 2px 10px;

    border-radius: 15px;
}

.app__hero-button button {
    color: white;
    font-size: 14px;
    width: 400;
    margin-left: 5px;
}

.app__hero-left {
    position: relative;
    width: 683px;
    height: 683px;
    background-color: var(--color-pink);
    border-radius: 50%;
       
}

.app__hero-img {
    display: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    transform: translateX(-0.5rem);
}

.app__hero-img img{
    transform: translateY(-5.1rem);
}

.app__hero-imgs {
    display: absolute;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: var(--color-voilete);
    width: 100%;
    height: 111px;
    border-radius: 30px;

    margin-bottom: 6rem;
    transform: translateY(-7rem);
}

.app__hero-imgs1 {
    display: flex;
}


@media screen and (max-width: 1150px) {
    .app__hero {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .app__hero-left {
        margin: 6rem 0;
    }

    .app__hero-right {
        margin-top: 5rem;
    }
}

@media screen and(max-wdith: 850px) {
    .app__hero {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .app__hero-left {
        position: relative;
        width: 483px;
        height: 483px;
        background-color: var(--color-pink);
        border-radius: 50%;

        margin:  3rem 0;
           
    }


}


