.app__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;

    position: sticky;
    margin-bottom: 1.5rem;

    padding: 1.8rem 5rem 0 4rem;
}

.app__navbar-logo {
   display: flex;
   justify-content: center;
   align-items: center;
}  

.app__navbar-links {
    display: flex;
    justify-content: center;
    align-items: center;
}

.app__navbar-links li {
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 0 1rem;
    cursor: pointer;
    font-weight: 600;
    font-size: 1rem;
}

.app__navbar-links li.signup a{
    background: var(--color-voilete);
    color: white;
    border-radius: 5px;
    height: 29px;
    width: 100px;
    border-radius: 40px;
    padding: 5px 15px 30px 20px;
}

